body{
    background-color: rgb(155, 159, 158);
    margin: auto;
    width: 50%;
    border: 3px solid rgb(25, 34, 51);
    padding: 10px;
    align-items: center;
    margin-top: 100px;
}

div.home{
    display: flex;
    /* padding: 5px 5px 5px 5px; */
    align-items: center;
    margin: auto;
    width: 50%;
    border: 3px solid rgb(25, 34, 51);
    padding: 0 20px 0 50px ;
}

div.log{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    margin-right: 20px;
}
div.log:hover{
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}
div.sign{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
}

div.sign:hover{
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

